body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body {
  background: #F7F8F9;
  min-height: 100vh;
  font-family: Arial, Helvetica, sans-serif;
}

* {
  box-sizing: border-box;
}

.tw--doc {
  padding: 20px 0;
}

.tw--page {
  position: relative;
  background: white;
  box-shadow: 0 3px 18px rgba(0, 0, 0, 0.09);
  margin: 20px auto 0;
  width: 800px;
  height: 1000px;
  padding: 60px;
  cursor: text;
  font-size: 18px;
  line-height: 36px;
}

.tw--cursor-selection {
  background: hsla(213, 100%, 50%, 0.2);
}

.tw--cursor-head {
  z-index: 10;
  width: 2px;
  background: hsla(213, 100%, 50%, 1);
  margin-left: -1px;
}

